export const updateLegalDocumentScrutinizedRowDataObj = (
  data: any,
  options?: { preDisbursementCollection?: boolean; postDisbursementCollection?: boolean }
): ILegalDocumentScrutinizedRowData => {
  const obj: ILegalDocumentScrutinizedRowData = {
    uid: data?.uid,
    loan: {
      uid: data?.loan?.uid,
    },
    stage: {
      uid: data?.stage?.uid,
    },
    applicant: {
      uid: data?.applicant?.uid,
    },
    dateOfDoc: data?.dateOfDoc !== ""? data?.dateOfDoc:null,
    name: data?.name,
    docCopyType: data?.docCopyType,
    preDisbursementCollection:
      options?.preDisbursementCollection ?? data?.preDisbursementCollection,
    postDisbursementCollection:
      options?.postDisbursementCollection ?? data?.postDisbursementCollection,
    isActive: data?.isActive,
  };
  return obj;
};

export const updateLegalDeviationsRowDataObj = (data: any): ILegalDeviationRowData => {
  const obj: ILegalDeviationRowData = {
    uid: data?.uid,
    loan: {
      uid: data?.loan?.uid,
    },
    stage: {
      uid: data?.stage?.uid,
    },
    actualDeviation: data?.actualDeviation,
    deviation: {
      uid: data?.deviation?.uid,
    },
    smDeviationType: data?.smDeviationType,
    mitigation: data?.mitigation,
    isActive: data?.isActive,
    smDeviationCategory: DEVIATION_CATEGORY.LEGAL_DEVIATIONS,
  };
  return obj;
};

export const updateLegalOtherProvisionObj = (data: any): ILegalOtherProvisionRowData[] => {
  const OtherProvisionList: ILegalOtherProvisionRowData[] = [];
  if (data && Array.isArray(data)) {
    for (const otherProvision of data) {
      OtherProvisionList.push({
        uid: otherProvision?.uid,
        loan: {
          uid: otherProvision?.loan?.uid,
        },
        stage: {
          uid: otherProvision?.stage?.uid,
        },
        smCode: otherProvision?.smCode,
        name: otherProvision?.name,
        value: otherProvision?.value,
        comment: otherProvision?.comment,
      });
    }
  }
  return OtherProvisionList;
};

export const updateUploadReportPageObj = (data: any): ILegalUploadReportPageObj => {
  const obj = {
    legalUid: data?.uid,
    savedDocuments: structLoanDocumentsFromAPIResponse(data?.loanDocuments),
  };
  return obj;
};

export const isLegalSectionAccessDisabled = (stage: string): boolean => {
  if (!stage) {
    return false;
  }
  if (stage === LOAN_STAGE_STATUS.COMPLETED || stage === LOAN_STAGE_STATUS.CANCELLED) {
    return true;
  }
  return false;
};
